#header-major .header-major__wrapper-logo {
  display: none;
  height: 110px;
  width: 110px;
  background-color: white;
  position: relative;
  margin: 0 0 0 40px;
}
#header-major .header-major__wrapper-logo::before {
  display: block;
  content: "";
  width: 25px;
  height: 110px;
  background-color: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%) skewX(-10deg);
  transform-origin: bottom;
}
#header-major .header-major__wrapper-logo::after {
  display: block;
  content: "";
  width: 25px;
  height: 110px;
  background-color: inherit;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(100%) skewX(-10deg);
  transform-origin: top;
}
#header-major .header-major__wrapper-logo img {
  width: 90%;
  display: block;
  z-index: 1000000000000000000;
}
#header-major .header-major__burger {
  display: block;
  position: relative;
  height: 27px;
  width: 38px;
  margin: 0 20px;
  z-index: 10000;
}
#header-major .header-major__burger__span {
  position: absolute;
  background-color: #ffffff;
  /* variables 5.1 */
  width: 100%;
  height: 3px;
  left: 0;
  top: 12px;
  transition: all 0.3s ease 0s;
}
#header-major .header-major__burger__span.active {
  transform: scale(0);
  background-color: #ffffff;
  /* variables 5.1 */
}
#header-major .header-major__burger::before,
#header-major .header-major__burger::after {
  content: "";
  background-color: #ffffff;
  /* variables 5.1 */
  position: absolute;
  width: 100%;
  height: 3px;
  left: 0;
  transition: all 0.3s ease 0s;
}
#header-major .header-major__burger::before {
  top: 0;
}
#header-major .header-major__burger::after {
  bottom: 0;
}
#header-major .header-major__burger.active::before {
  transform: rotate(45deg);
  top: 12px;
  background-color: #ffffff;
  /* variables 5.1 */
}
#header-major .header-major__burger.active::after {
  transform: rotate(-45deg);
  bottom: 12px;
  background-color: #ffffff;
