/* header-major ========================================== */
#header-major {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #040406;
  /* variables 4.2 */
}
#header-major .header-major__wrapper-logo {
  display: none;
  height: 110px;
  width: 110px;
  background-color: white;
  position: relative;
  margin: 0 0 0 40px;
}
#header-major .header-major__wrapper-logo::before {
  display: block;
  content: "";
  width: 25px;
  height: 110px;
  background-color: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100%) skewX(-10deg);
  transform-origin: bottom;
}
#header-major .header-major__wrapper-logo::after {
  display: block;
  content: "";
  width: 25px;
  height: 110px;
  background-color: inherit;
  position: absolute;
  top: 0;
  right: 0;
  transform: translateX(100%) skewX(-10deg);
  transform-origin: top;
}
#header-major .header-major__wrapper-logo img {
  width: 90%;
  display: block;
  z-index: 1000000000000000000;
}
#header-major .header-major__burger {
  display: block;
  position: relative;
  height: 27px;
  width: 38px;
  margin: 0 20px;
  z-index: 10000;
}
#header-major .header-major__burger__span {
  position: absolute;
  background-color: #ffffff;
  /* variables 5.1 */
  width: 100%;
  height: 3px;
  left: 0;
  top: 12px;
  transition: all 0.3s ease 0s;
}
#header-major .header-major__burger__span.active {
  transform: scale(0);
  background-color: #ffffff;
  /* variables 5.1 */
}
#header-major .header-major__burger::before,
#header-major .header-major__burger::after {
  content: "";
  background-color: #ffffff;
  /* variables 5.1 */
  position: absolute;
  width: 100%;
  height: 3px;
  left: 0;
  transition: all 0.3s ease 0s;
}
#header-major .header-major__burger::before {
  top: 0;
}
#header-major .header-major__burger::after {
  bottom: 0;
}
#header-major .header-major__burger.active::before {
  transform: rotate(45deg);
  top: 12px;
  background-color: #ffffff;
  /* variables 5.1 */
}
#header-major .header-major__burger.active::after {
  transform: rotate(-45deg);
  bottom: 12px;
  background-color: #ffffff;
  /* variables 5.1 */
}
#header-major .header-major__menu__nav {
  display: block;
  width: 100%;
  height: 100vh;
  position: absolute;
  padding: 70px 0 70px 0;
  top: 0;
  left: -100%;
  overflow: auto;
  background-color: #000000;
  /* variables 4.3 */
  transition: all 0.3s ease 0s;
  z-index: 1000;
}
#header-major .header-major__menu__nav__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0 auto;
  width: 90%;
  padding: 0;
}
#header-major .header-major__menu__nav__list__link {
  display: block;
  width: 90%;
  text-decoration: none;
}
#header-major .header-major__menu__nav__list__link__item {
  list-style-type: none;
  margin: 20px 0;
  text-transform: uppercase;
  font-size: 20px;
  /* variables 2.3 */
  color: #ffffff;
  /* variables 3.1 */
  font-family: 'Kelly Slab', cursive;
  /* variables 1.2 */
}
#header-major .header-major__menu__nav__list__link__item.active-page {
  color: #2caba8;
  /* variables 3.2 */
  border-bottom: 2px solid #2caba8;
  /* variables 3.2 */
}
#header-major .header-major__menu__nav.open-nav {
  left: 0;
}
#header-major .header-major__contacts {
  width: 60%;
  padding: 0;
}
#header-major .header-major__contacts__list {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  flex-direction: column;
  width: 100%;
  padding: 0 5px 0 5px;
}
#header-major .header-major__contacts__list__link {
  text-decoration: none;
}
#header-major .header-major__contacts__list__link__item {
  list-style-type: none;
  color: #ffffff;
  /* variables 3.1. */
  font-family: 'Kelly Slab', cursive;
  margin: 10px 10px 0 0;
}
#header-major.header-black {
  background-color: black;
}
